'use strict';

(function () {
  let menu = document.querySelector('.main-navigation');
  let menuToggle = document.querySelector('.main-navigation__toggle');
  let links = menu.querySelectorAll('.main-navigation__link');
  let cardsLink = document.querySelectorAll('.country-card__content-link');
  let control = document.querySelector('.countries-information__control-list');

  menu.classList.remove('main-navigation--no-js');
  menuToggle.addEventListener('click', function() {
    menu.classList.toggle('main-navigation--open');
  });

  links.forEach(function (elem) {
    elem.addEventListener('click', function () {
      menu.classList.remove('main-navigation--open');
    })
  });

  cardsLink.forEach(function (elem) {
    elem.addEventListener('click', function (evt) {
      evt.preventDefault();
      let target = evt.target;
      while (true) {
        if (target.hasAttribute('data') || target === elem) {
          break;
        } else {
          target = target.parentNode;
        }
      }
      let slideName = document.querySelector(`[data-name='${target.dataset.card}']`);;
      let activeSlideName = document.querySelector('.countries-information__slide-name--active');
      let slide = document.querySelector(`[data-slide='${target.dataset.card}']`);
      let activeSlide = document.querySelector('.countries-information__slide-item--active');
      window.fullSlider.changeSlide(slideName, activeSlideName, slide, activeSlide);
      control.scrollIntoView();
    })
  });
})();
